@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400&display=swap);
/* Key imports should go here */

/* Setting global css variables, we also set the base font-size for all our rems here */
:root{ 
  font-size: 16px;
  --primary-one: #5f0a87;
  --primary-two: #a4508b;
  --secondary-one: #fff;
  --secondary-two: #000;
  --secondary-three: #434343;
  --button-padding: 0.5rem 1rem;
  --window-width: 100vw;
  --window-height: 100vh;
  --card-padding: 3rem 5rem 3rem 4rem;
  --thin-card-padding: 2rem 3rem 2rem 3rem;
  --error-modal-padding: 0rem 5rem 1rem 4rem;
  --error-modal-thin-padding: 0rem 3rem 1rem 3rem;
  --card-corners: 1rem;
}

body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


  .userbuilder-components{
    height: 100vh;
    margin: auto;
    background-color: var(--secondary-three);
  }

  .userbuilder-components.errorMode{
    background-color: var(--secondary-two);
    z-index: 2;
    opacity: 1;
  }

  .userbuilder-components .hideComponent{
    display: none;
    color: var(--secondary-one);
  }

  .userbuilder-components .opaque-overlay{
    background-color: rgba(0, 0, 0, 1);
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 3;
    opacity: 0.3;
  }

  button{
    padding: var(--button-padding);
    background-color: var(--primary-one);
    border: 1px solid var(--primary-one); 
    border-radius: 0.3rem;
    margin-bottom: 1rem;
    margin-left: 1.25%;
    margin-top: 1rem;
    font-size: 1.2rem;
    font-weight: 500;
    color: var(--secondary-one);
    transition: all 0.20s linear;
    cursor: pointer;
  }

  button:hover{
    background-color: var(--primary-two);
  }

  @media screen and (max-width: 768px){

    button{ 
      width: 100%; 
    }

    .userbuilder-components{
      height: 130vh;
    }

    .userbuilder-components.errorMode{
      height: 110vh;
    }

  }




.userbuilder-form{
    width: 50%;
    margin: auto;
    position: relative;
    top: 5rem;
    background-color: var(--secondary-one);
    border-radius: var(--card-corners);
    padding: var(--card-padding);
    z-index: 1;

}

.opaque-overlay .userbuilder-form{
    -webkit-transform: translateZ(-10px);
            transform: translateZ(-10px);
}

.userbuilder-form label{
    width: 100%;
    color: var(--secondary-two);
    font-size: 1.5rem;
    font-weight: 500;
    margin: 0.5rem auto 0.5rem auto;
    display: block;
    margin-left: 1.25%;
}

.userbuilder-form input{
    width: 95%;
    margin: auto;
    margin-bottom: 2rem;
    display: block;
    padding: 0.75rem 0.5rem;
    transition: all 0.10s linear;
    border: 2px solid var(--secondary-two);
    border-radius: 3px;
    font-size: 1.2rem;
}

.userbuilder-form form input:hover, .userbuilder-form form input:active{
    border-color: var(--primary-one);
    
}

.userbuilder-form form input:focus{
    border-color: var(--primary-one);
    background-color: var(--primary-one);
    color: var(--secondary-one);
}

@media screen and (max-width: 768px){
    .userbuilder-form{
        width: 60%;
        padding: var(--thin-card-padding);
    }

    .userbuilder-form input{
        width: 90%;
    }
}
.user-list-section{
    width: 50%;
    margin: auto;
    margin-top: 10rem;
    position: relative;
    background-color: var(--secondary-one);
    padding: var(--card-padding);
    border-radius: var(--card-corners);
}

.user-list-section ul{
    list-style-type: none;
    padding: 0rem;
}

.user-list-section ul li{
    border: 2px solid grey;
    border-radius: 0.3rem;
    margin: 1.5rem 0rem;
    padding: 0.5rem;
    transition: all 0.10s linear;
    cursor: pointer;
}

.user-list-section ul li:hover, .user-list-section ul li:focus{
    border-color: var(--primary-one);
    color: var(--primary-one);
}

@media screen and (max-width: 768px){
    .user-list-section{
        width: 60%;
        padding: var(--thin-card-padding);
    }
}
.errorModal{
    width: 50%;
    margin: auto;
    background-color: var(--secondary-one);
    border-radius: var(--card-corners);
    padding: var(--error-modal-padding);
    position: absolute;
    left: calc(25% - 4.5rem);
    top: 25rem;
    z-index: 4;
}

.errorModal h1{
    color: var(--secondary-one);
    background-color: var(--primary-one);
    position: relative;
    right: 4rem;
    bottom: 1.4rem;
    width: calc(100% + 7rem);
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
    padding: 1rem;
}

.errorModal button{
    display: flex;
    margin-left: auto;
}

@media screen and (max-width: 768px){
    .errorModal{
        width: 59.5%;
        left: calc(29% - 5rem);
        padding: var(--error-modal-thin-padding);
    }

    .errorModal h1{
        right: 3rem;
        width: calc(100% + 4rem);
    }
}


